import { all, put, takeEvery, takeLatest } from "redux-saga/effects";
import {
  getAllApplication,
  getSelctedAppDetailsAPI,
  onboardNewAppAPI,
  fetchData,
  getAllReportsData,
  updateStatusData,
  updateStatusAppAPI,
  samplePayloadData,
  addNotificationAPI,
  channelListAPI,
  notificationListAPI,
  getTemplateDetailAPI,
  updateTemplateAPI,
  categoryListAPI,
  getRecipientsAPI,
  testSamplePayloadAPI,
  getProfileInfoAPI
} from "../../api/endpoints";
import actions from "../actions";

// Function to get the App list
function* getAllApplications() {
  try {
    const data = yield getAllApplication().then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.setAllApplication(data.applications));
  } catch (error) {
    console.log(error);
  }
}

function* getSelctedAppDetails(payload) {
  try {
    const data = yield getSelctedAppDetailsAPI(payload).then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.setSelectedApp(data));
    yield put(actions.setSelectedAppStatus(data.active));
    //This action has been moved to saga getAllNotificationTemplateData (line no: 66,67)
    // yield put(actions.setAllTempData([]));
    // yield put(actions.setAllNotificationData([]));
    yield put(actions.setAllReports([]));
  } catch (error) {
    console.log(error);
  }
}

// Function to create/onboard New App
function* onboardNewApp(payload) {
  try {
    const data = yield onboardNewAppAPI(payload).then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.postSucess(data.success));
  } catch (error) {
    yield put(actions.setAPIFailMessage(error.response.data.error.message[0]));
    yield put(actions.postSucess(false));

    console.log(error);
  }
}
// Function to fetch Manage Templates data
function* getAllNotificationTemplateData(payload) {
      yield put(actions.setAllNotificationData([]));
      yield put(actions.setAllTempData([]));


  try {
    const data = yield fetchData(payload).then(dataFromApi => dataFromApi.data);
    if (payload.payload.tab === "notification") {
      yield put(actions.setAllNotificationData(data));
    } else {
      yield put(actions.setAllTempData(data));
    }
  } catch (error) {
    if (payload.payload.tab === "notification") {
      yield put(actions.setAllNotificationData([]));
    } else {
      yield put(actions.setAllTempData([]));
    }
  }
}

// Function to fetch Manage Reports data
function* getAllReports(payload) {
  try {
    const data = yield getAllReportsData(payload).then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.setAllReports(data));
  } catch (error) {
    yield put(actions.setAllReports([]));
  }
}

// Function to change status of Notification and template
function* updateStatus(payload) {
  // yield put(actions.statusFailedId(""));
  try {
    const data = yield updateStatusData(payload).then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.statusUpdateSuccess(true));
    payload.payload.refreshData();
  } catch (error) {
    payload.payload.tryThis(payload.payload.typeId);
    yield put(actions.statusUpdateSuccess(false));

    alert(
      "Notiification can not be deactivated as it is associated with one or more active template(s)"
    );
  }
}
// Function to change status of Application

function* updateStatusApp(payload) {
  try {
    const data = yield updateStatusAppAPI(payload).then(
      dataFromApi => dataFromApi.data
    );
    payload.payload.refreshData(payload.payload.appId);
    
  } catch (error) {
    // yield put(actions.statusUpdateSuccess(false));
    alert("App can't deactivate as there is one or more active notifications");
    payload.payload.tryThis(payload.payload.appId);
  }
}

// Function to fetch payload data in manage template
function* getPayload(payload) {
  try {
    const data = yield samplePayloadData(payload).then(
      dataFromApi => dataFromApi.data
    );
    yield put(actions.setSamplePayload(data));
  } catch (error) {
    yield put(actions.setSamplePayload([]));
    alert(error.message);
  }
}

