import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MoreIcon from '@material-ui/icons/MoreVert';
import Logo from './logo';
import ProfileInfo from './profileInfo';
import CustomMenu from './menuDrawer';

/* AppBar component */
export default function PrimarySearchAppBar({siteTitle}) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = event => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const menuId = 'profile-menu';
  const renderMenu = (
    <Menu
    className ="profileMenu"
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Logout</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'profile-menu-mobile';
  const renderMobileMenu = (
    <Menu
      className="MobileProfileMenu"
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
    <ProfileInfo mobileMenu menuId = {menuId} handleProfileMenuOpen = {handleProfileMenuOpen} setAnchorEl = {setAnchorEl}/>
    </Menu>
  );

  return (
    <div className="grow">
      <AppBar position="static" className="AppBar" data-test='AppBar'>
         <Toolbar>
           <CustomMenu/>
           <Logo/>
           <Typography className="title"  variant="h6" noWrap>
             {siteTitle}
           </Typography>
           <div className='grow' />
           <div className='sectionDesktop'>
              <ProfileInfo menuId = {menuId} handleProfileMenuOpen = {handleProfileMenuOpen} setAnchorEl = {setAnchorEl}/>
           </div>
           <div className='sectionMobile'>
             <IconButton
               aria-label="show more"
               aria-controls={mobileMenuId}
               aria-haspopup="true"
               onClick={handleMobileMenuOpen}
               color="inherit"
             >
               <MoreIcon />
             </IconButton>
           </div>
         </Toolbar>
       </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </div>
  );
}