import React , { useEffect , useState } from "react";
import { useSelector,useDispatch } from "react-redux";
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Typography from '@material-ui/core/Typography';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import IconButton from '@material-ui/core/IconButton';
import { Link } from "gatsby";
import ProfileImage from '../sections/ProfileImage';
import actions from "../../store/actions";

const useStyles = makeStyles(theme => ({
    removeGap: {
        padding: '0'
      } ,
    welcomeTxt :{
        fontSize:"0.667rem !important",
        color:theme.palette.textPrimary,  
    }
}));

/* Profile Info component */
const ProfileInfo = (props) => {
    const classes = useStyles();
    const profileID = 633980;
    const textType = props.mobileMenu ? "textPrimary":"textSecondary";
    const [ profile , setProfile ] = useState(null);
    const data = useSelector(state => state.notification);
    
    const dispatch = useDispatch();
    const profileData = !!data ? ( !!data.profileInfo ? data.profileInfo[0] : null ) : (null);
    useEffect(() => {
        setProfile(profileData);
    },[profileData]);

    useEffect(() => {
        dispatch(actions.getProfileInfo());
    },[]);

   
    return (
        !!profile &&
        <List className="profileInfo" data-test='profileInfo'>
            <ListItem alignItems="flex-start" className={classes.removeGap}>
                <ListItemAvatar
                    edge="end"
                    aria-label="account of current user"
                    aria-controls={props.menuId}
                    aria-haspopup="true"
                    color="inherit"
                >
                <ProfileImage profileID = {profile.profileId} />
                {/* <ProfileImage profileID = {profileID} /> */}
                </ListItemAvatar>
                <ListItemText
                    edge="end"
                    aria-label="account of current user"
                    aria-controls={props.menuId}
                    aria-haspopup="true"
                    color="inherit"
                    primary={
                        <React.Fragment>
                            <Typography
                                component="span"
                                color={textType}
                                className={classes.welcomeTxt}
                            >
                            <span>Welcome</span>
                        </Typography>
                        </React.Fragment>
                    }
                    secondary={
                        <React.Fragment>
                            <Typography
                                component="span"
                                color={textType}
                            >
                            <span>{profile.firstName}&nbsp;{profile.lastName}</span>
                        </Typography>
                        </React.Fragment>
                    }
                    />
                {
                    !props.mobileMenu &&
                    <IconButton
                        edge="start"
                        className="arrowButton"
                        color="inherit"
                        aria-label="open drawer"
                        onClick={props.handleProfileMenuOpen}>
                        <ArrowDropDownIcon/>
                    </IconButton>
                }
            </ListItem>
            {   props.mobileMenu &&
                <ListItem>
                    <Link to={`/app`} >
                    <Typography variant="subtitle2" color={textType} gutterBottom>Logout</Typography>
                    </Link>
                </ListItem>
            }
      </List>
    )
    
}
export default ProfileInfo