import React,{ useEffect,useState,Fragment }  from 'react';
import Avatar from '@material-ui/core/Avatar';
import axios from 'axios';

/* Custom Profile Image Component */
/* Used in Application header Profile Info and Manage Reports Recipient list */
const ProfileImage = ({profileID}) => {

    const profile_image_path = process.env.PROFILE_IMAGE_PATH + profileID +'.JPEG';
    const default_image_path = process.env.PROFILE_IMAGE_PATH +'default.jpg';
    const [hasImage,setHasImage] = useState(false);

    useEffect(() => {
        if(profileID){
            axios.get(profile_image_path, { responseType:"blob" }).then( r => {
                setHasImage(true);
            }).catch(e => {
                console.error(e);
                setHasImage(false);
            })
        } else {
            setHasImage(false);
        }
        
    },[profileID]);

    return (
       <Fragment>
        {
            !!hasImage ? <Avatar alt="Profile Pic" src={profile_image_path}  /> : <Avatar alt="Profile Pic" src={default_image_path}/> 
        }
        </Fragment>  
    )
}

export default ProfileImage
